// const assert = require('assert')

const UNITS = {
    px: 'px',
    pct: '%',
    cm: 'cm'
}

const Measurement = (value, unit, extra = {}) => {
    // assert(unit in UNITS., `Invalid Unit: ${unit}`)

    const rendered = `${value}${unit}`

    return {
        value: value,
        unit: unit,
        rendered: rendered,
        HALF: () => Measurement(value/2, unit),
        ...extra
    }
}

const MEASUREMENTS = {
    BORDER: Measurement(2, UNITS.px),
    BORDER_RADIUS: Measurement(10, UNITS.px,{
        BUTTON: Measurement(100000000000000, UNITS.px)
    }),
    MARGIN: Measurement(40, UNITS.px),
    PADDING: Measurement(10, UNITS.px),
    BOX_SIZE: Measurement(1, UNITS.cm, {
        FONT_SIZE: Measurement(0.8, UNITS.cm)
    })
}

const HACK_BOX = {
    WIDTH: Measurement(50, UNITS.pct),
    MIN_WIDTH: Measurement(100, UNITS.px)
}

const COLORS = {
    PINK: '#ff4874',
    RED: '#ff0000',
    BACKGROUND: '#1c1c1c',
    WHITE: '#eaeaea'
}

module.exports = {
    MEASUREMENTS,
    COLORS,
    UNITS,
    HACK_BOX
}