import styled, { css } from 'styled-components'
const {COLORS, MEASUREMENTS} = require('../components/_constants')
const { CaptionText } = require('./text')


export const Input = styled.input`
  width: ${MEASUREMENTS.BOX_SIZE.rendered};
  height: ${MEASUREMENTS.BOX_SIZE.rendered};
  font-size: ${MEASUREMENTS.BOX_SIZE.FONT_SIZE.rendered};
  border-width: 1px;
  border-style: solid;
  border-color: ${props => props.error ? COLORS.RED : COLORS.WHITE};
  color: ${COLORS.PINK};
  margin-left: auto;
  margin-right: auto;
  background-color: transparent;
  border-radius: ${MEASUREMENTS.BORDER_RADIUS.rendered};
  text-align: center;
  margin-top: ${MEASUREMENTS.MARGIN.HALF().rendered};
  margin-bottom: ${MEASUREMENTS.MARGIN.HALF().HALF().rendered};
  display: block;
`

const InputContainer = styled.div`
  margin-left: auto;
  margin-right: auto;
  margin-top: ${MEASUREMENTS.MARGIN.HALF().rendered};
  margin-bottom: ${MEASUREMENTS.MARGIN.HALF().rendered};
`
export const CaptionedInput = (text) => {
    return (
        <InputContainer>
            <Input label={text} type="text"/>
            <CaptionText>{text}</CaptionText>
        </InputContainer>
    )
}